.stat-item__container {
  display: grid;
  grid-template-columns: 125px auto 7px;
  grid-template-rows: 120px;
  grid-template-areas: 
    "name progress btn";

  grid-gap: 25px;
}

.stat-item__name-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  grid-area: name;
}

.stat-item__name {
  font-family: Fira Sans;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: -0.9px;
  margin-bottom: 10px;
}

.stat-item__star-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.stat-item__progress-bar-container {
  grid-area: progress;
  position: relative;
}

.stat-item__progress-bar {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.stat-item__minion-icon {
  position: absolute;
  right: 0;
  top: -70px;
}

.stat-item__line {
  height: 7px;
  background-color: #94e100;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
}

.stat-item__indicator {
  -ms-flex: 0 0 50px;
      flex: 0 0 50px;
  height: 25px;
  border-radius: 10px;
  background-color: #94e100;
  border: solid 3px #ffffff;

  font-family: Fira Sans;
  font-size: 16px;
  font-weight: bold;
  letter-spacing: -0.8px;
  text-align: center;
  color: black;
  vertical-align: middle
}

.stat-item__btn-container {
  grid-area: btn;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
}

.stat-item__btn {
  
}


