.container {
  display: grid;

  width: 100vw;
  height: 100vh;
  
  grid-template-columns: 1fr 3fr;
  grid-template-rows: 55px auto;
  grid-template-areas: 
    "header header"
    "nav main"
}

main {
  grid-area: main;
}

header {
  grid-area: header;
}

nav {
  grid-area: nav;
}


